import {
    AllHostingOptions,
    ConsistencyModels,
    DbModels,
    HostingOptions,
    IDatabase,
    IExtension,
    Licenses,
    SecondaryModelSupport,
    SupportType
} from "../types";
import {RelationalDatabase} from "./RelationalDatabase";

export class PgSql extends RelationalDatabase implements IDatabase {
    get isCommercial() {
        return false;
    }

    get secondaryModels() {
        return [
            {
                type: DbModels.Document,
                support: SupportType.Supported
            },
            {
                type: DbModels.Graph,
                // TODO: check
                support: SupportType.PartialSupport
            },
            {
                type: DbModels.Spatial,
                // TODO: check
                support: SupportType.PartialSupport
            },
            {
                type: DbModels.Vector,
                // TODO: check
                support: SupportType.PartialSupport
            }
        ];
    }

    get name() {
        return "PostgreSQL";
    }

    get description() {
        return "Widely used open source RDBMS";
    }

    get consistencyModels() {
        return [ConsistencyModels.ImmediateConsistency];
    }

    get license() {
        return Licenses.Free;
    }

    get hostingOptions() {
        return AllHostingOptions;
    }

    get indexes() {
        return "Primary, Secondary";
    }

    get transactions() {
        return 'ACID';
    }

    get scheme() {
        return 'On write';
    }

    get replication() {
        return 'Source-replica replication';
    }

    get released() {
        return 1989;
    }
}

export class Citus implements IExtension {
    get name(): string {
        return "Citus";
    }

    get models(): SecondaryModelSupport[] {
        return [
            {
                type: DbModels.Columnar,
                support: SupportType.PartialSupport
            }
        ];
    }

    get description(): string {
        throw new Error("Method not implemented.");
    }

}