import {DbModels} from "../types";
import {Database} from "../shared/Database";

export abstract class RelationalDatabase extends Database {
    get model() {
        return DbModels.Relational;
    }
    
    get pc() { return true }

    get ec() { return true }

    get sharding() {
        return 'No';
    }

    get sql(){
        return 'Yes';
    }
    
    get apis(){
        return 'ADO.NET, JDBC, ODBC';
    }
}