import {AllHostingOptions, ConsistencyModels, IDatabase, Licenses} from "../types";
import {DocumentDatabase} from "./DocumentDatabase";

export class MongoDb extends DocumentDatabase implements IDatabase {
    get consistencyModels(): ConsistencyModels[] {
        return [ConsistencyModels.ImmediateConsistency, ConsistencyModels.EventualConsistency];
    }
    get isCommercial() {
        return false;
    }

    get secondaryModels() {
        return [];
    }

    get name() {
        return "MongoDB";
    }

    get description() {
        return "One of the most popular document stores available both as a fully managed cloud service and for deployment on self-managed infrastructure.";
    }
    
    get license() {
        return [];
    }
    
    get hostingOptions() {
        return AllHostingOptions;
    }
    
    get apis(){
        return [
            'GraphQL',
            'HTTP REST',
            'Prisma',
            'proprietary protocol using JSON'     
        ];
    }
    
    /*
    Spatial DBMS
    Search engine info
    Time Series DBMS info
    Vector DBMS info
     */
}